.custom-select__list {
  position: absolute;
  bottom: 10px;
  z-index: 1;
  width: 100%;
  max-height: 100px;
  margin: 0;
  padding: 0;
  overflow-y: auto;
  list-style: none;
  background-color: #ffffff;
