.engine-table {
  width: 985px;
  margin-bottom: 40px;
}
.engine-table table {
  border-collapse: separate;
  border-spacing: 5px;
  width: 100%;
}
.engine-table tbody tr:nth-child(odd) {
  background-color: #f0f3f7;
}
.engine-table tbody tr:nth-child(even) {
  background-color: #e6ecf3;
}
.engine-table thead tr {
  background-color: #0f64da;
}
.engine-table table td,
.engine-table table th {
  padding-top: 15px;
  padding-bottom: 15px;
  padding-left: 20px;
  padding-right: 20px;
}
.engine-table table tr {
  display: flex;
  align-items: center;
  width: 100%;
  justify-content: space-between;
  margin-bottom: 5px;
}
.engine-table table tr th,
.engine-table table tr td {
  min-width: 127px;
}
.engine-table table tr th:first-of-type,
.engine-table table tr td:first-of-type {
  min-width: 100px;
}
.engine-table table tr th:nth-child(2),
.engine-table table tr th:last-of-type,
.engine-table table tr td:nth-child(2),
.engine-table table tr td:last-of-type {
  min-width: 300px;
}
.engine-table table tr td:last-of-type {
  display: flex;
}
.engine-table table thead {
  background-color: #0f64da;
}
.engine-table table th {
  padding-top: 15px;
  padding-bottom: 15px;
  font-size: 14px;
  line-height: 20px;
  color: #ffffff;
  display: flex;
  align-items: center;
  text-align: left;
}
.engine-table table th svg {
  min-width: 8px;
  min-height: 4px;
  margin-left: 5px;
  fill: white;
}
.engine-table table th:first-of-type, .engine-table table th:last-of-type {
  width: 100px;
}
.engine-table table th:first-of-type svg, .engine-table table th:last-of-type svg {
  display: none;
}
.engine-table table tr td:last-of-type {
  justify-content: space-between;
}

